body {
	
    background-color: #fafafa;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-weight: 300;
    margin-bottom: 60px;
}


h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
 font-family: "Roboto", sans-serif;
 font-weight: 600;
 color: #333;
}



.btn-primary {
 background-color: #3d6fbd;
 border-color: #3d6fbd ;
}


.hoverButton {
 display: none; 
}


.myRow:hover .hoverButton {
  display: inline; 
}

.myRow:hover {
  background-color: #f9f9f9
}


.input-group-addon.success {
   color: rgb(255, 255, 255);
   background-color: rgb(92, 184, 92);
   border-color: rgb(76, 174, 76);
}


.myPanel {
 margin-top: 10px; 
 margin-bottom:10px;
 border-style:solid; 
 border-width: 0.5px; 
 border-color: #eaeaea; 
 border-radius:3px; 
 background-color:white;
 box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, .05);
}


@media screen and (max-width: 768px){
     .hoverButton{display:none !important;}
     div.pull-right.btn.btn{display:none !important;}
     .hoverButton{display:none !important;} 
     div.col-md-8.col-md-offset-2{margin:5px !important;
                                   padding:0px !important;}

   }



span.input-group-addon.btn.btn-success.success:hover {
         background-color:#3dff3d;
}   

